@font-face {
  font-family: 'new-cv';
  src:
    url('asset/font/new-cv.ttf?iupam5') format('truetype'),
    url('asset/font/new-cv.woff?iupam5') format('woff'),
    url('asset/font/new-cv.svg?iupam5#new-cv') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="new-cv-icon-"], [class*=" new-cv-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'new-cv' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

